.headingSection {
  flex-direction: column;
  margin: 3rem 0;
}

.headingSection .containers {
  height: 50rem;
  width: 100%;
  justify-content: space-around;
}

.headingSection .containers .allCont {
  height: 45rem;
  width: 37rem;
  background-color: rgba(217, 217, 217, 1);
}

.headingSection .containers img {
  display: none;
}

.headingSection .containers .container1 {
  box-shadow: -1.2rem 1.2rem 0.1rem 0.3rem #ffb61d;
}

.headingSection .containers .container2 {
  box-shadow: -1.2rem 1.2rem 0.1rem 0.3rem #75c056;
}

.headingSection .containers .container3 {
  box-shadow: -1.2rem 1.2rem 0.1rem 0.3rem #e86637;
}


/* Responsive for Phone */
@media screen and (max-width: 620px) {
  .headingSection .containers {
    width: 80%;
    justify-content: space-evenly;
  }

  .headingSection .containers .allCont {
    width: 35rem;
  }

  .headingSection .containers .container3 {
    display: none;
  }

  .headingSection .containers .container2 {
    display: none;
  }

  .headingSection .containers .container1 {
    order: 1;
    width: 40rem;
    height: 48rem;
    box-shadow: 1.3rem 1.1rem .1rem .3rem rgba(255, 182, 29, 1);
  }

  .headingSection .containers img {
    display: block;
    width: 33rem;
  }
}